.gpt3__possibility {
    display: flex;
    flex-direction: row;
}

.gpt3__possibility-image {
    flex: 1; 
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-right: 2rem;
}

.gpt3__possibility-image img {
    width: 100%;
    height: 100%;
}

.gpt3__possibility-content {
    flex: 1; 
    display: flex;
    flex-direction: column;
    justify-content: flex-end; 
    align-items: flex-start;
}

.gpt3__possibility-content h4 {
    color: #71E5FF;
    font-family: var(--font-family);
    font-size: 1rem;
    font-weight: 500;
    line-height: 1.875rem; 
}

.gpt3__possibility-content h4:last-child {
    color: #FF8A71;
}

.gpt3__possibility-content h1 {
    font-family: var(--font-family);
    font-size: 2.125rem;
    font-weight: 800;
    line-height: 2.8125rem;
    margin: 1rem 0;
}

.gpt3__possibility-content p {
    color: var(--color-text);
    font-family: var(--font-family);
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.875rem;
    margin-bottom: 2rem;
}

@media screen and (max-width:950px){
    .gpt3__possibility {
        flex-direction: column;
    }
    .gpt3__possibility img {
        margin: 1rem 0;
    }
    .gpt3__possibility-content {
        margin-top: 2rem;
    }
}